import { Collapse } from "antd";
import { CInput, CSelect, CTextArea, CSwitch, CDatePicker } from "@components";

const CFixAssetLocation = ()=>{
    const {Panel} = Collapse;

    return (
        <>
            <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
                <Panel header="Location" key="0">
                  <CSelect
                     isRequired
                     label="Location"
                     id="location_id"
                     name="location_id"
                  />
                  <CTextArea
                     label="Location Memo"
                     id="location_memo"
                     name="location_memo"
                  />
                  <CInput
                     label="Room Number"
                     name="room_number"
                     id="room_number"
                     isRequired
                     autoFocus
                  />

                  <CSelect
                     isRequired
                     label="Department"
                     id="department"
                     name="department"
                  />

                  <CSelect
                     isRequired
                     label="Responsible"
                     id="responsible"
                     name="responsible"
                  />

                  <CInput
                     label="Barcode"
                     name="barcode"
                     id="barcode"
                     isRequired
                     autoFocus
                  />

                  <CSwitch
                     label="Missing Asset"
                     name="missing_asset"
                     id="missing_asset"
                     isRequired
                     autoFocus
                  />

                  <CDatePicker
                     label="Physical Inventory"
                     name="physical_inventory"
                     id="physical_inventory"
                     isRequired
                     autoFocus
                  />
                  
                </Panel>
            </Collapse>
        </>
    )
}

export default CFixAssetLocation;